a, body, div, footer, form, h1, h2, h3, h4, h5, h6, header, html, img, input, li, p, section, select, span, ul {
  margin: 0;
  padding: 0; }

li, ol, ul {
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

a img, img {
  border: 0 none;
  vertical-align: top; }

input {
  outline: none; }

button {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #333; }

a, a:focus {
  outline: none; }

body, html {
  width: 100%;
  font-family: PingFang SC,Droid Sans Fallback,Microsoft YaHei,u5FAEu8F6Fu96C5u9ED1,Arial,SimSun,u5B8Bu4F53; }

body {
  background: #fff;
  position: relative;
  font-size: 16px; }

.clearfix:after {
  content: "";
  display: block;
  clear: both; }

.clearfix {
  zoom: 1; }

img {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent; }
